/* eslint-disable @typescript-eslint/no-explicit-any */
if ((global as any).__platformBundles !== undefined) {
  const platformBundles = (global as any).__platformBundles.concat();
  Reflect.deleteProperty(global, '__platformBundles');
  for (let i = 0; i < platformBundles.length; ++i) {
    console.log(`PB start ${i + 1}/${platformBundles.length}`);
    (global as any).eval(platformBundles[i]);
    console.log(`PB done  ${i + 1}/${platformBundles.length}`);
  }
}
